/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { useEffect } from '@wordpress/element';
import {
	RichText,
	InnerBlocks,
	PanelColorSettings,
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, TextControl, CheckboxControl } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import deprecated from './deprecated';
import { ArkbProOnly } from '@components';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * ステップ項目
 */
const blockName = 'ark-block-step';
registerBlockType(metadata.name, {
	title: __('Step item', 'arkhe-blocks'),
	icon: blockIcon,
	edit: (props) => {
		const { attributes, setAttributes, context } = props;
		const { title, numColor, theLabel, theNum, hideContent } = attributes;

		// context
		const isHideLabel = context['arkb/step/isHideLabel'];
		const isHideNum = context['arkb/step/isHideNum'];
		const stepLabel = context['arkb/step/stepLabel'];

		// context変更時の処理
		useEffect(() => {
			if (attributes.isHideLabel !== isHideLabel) {
				setAttributes({ isHideLabel });
			}
			if (attributes.isHideNum !== isHideNum) {
				setAttributes({ isHideNum });
			}
			if (attributes.stepLabel !== stepLabel) {
				setAttributes({ stepLabel });
			}
		}, [isHideLabel, isHideNum, stepLabel]);

		// ステップ番号の色設定
		let numStyle = null;
		if (numColor) {
			numStyle = { '--arkb-step-color': numColor };
		}

		const blockProps = useBlockProps({
			className: `${blockName}__item`,
		});
		const innerBlocksProps = useInnerBlocksProps(
			{
				className: `${blockName}__body ark-keep-mt--s`,
			},
			{
				template: [['core/paragraph']],
				templateLock: false,
			}
		);

		const thisStepLabel = isHideLabel ? '' : theLabel || stepLabel;
		const thisStepNum = isHideNum ? '' : theNum || null;

		return (
			<>
				<InspectorControls>
					<PanelColorSettings
						title={__('Color settings', 'arkhe-blocks')}
						initialOpen={true}
						colorSettings={[
							{
								value: numColor,
								label: __('Color', 'arkhe-blocks'),
								onChange: (value) => {
									setAttributes({ numColor: value });
								},
							},
						]}
					/>
					<PanelBody title={__('Text settings', 'arkhe-blocks')}>
						<ArkbProOnly>
							<CheckboxControl
								label={__('Hide content', 'arkhe-blocks')}
								checked={hideContent}
								onChange={(val) => setAttributes({ hideContent: val })}
							/>
							{!isHideLabel && (
								<TextControl
									label={__('Text of "STEP" part', 'arkhe-blocks')}
									value={theLabel || stepLabel || ''}
									onChange={(val) => {
										if (!IS_PRO) return;
										setAttributes({ theLabel: val });
									}}
								/>
							)}
							{!isHideNum && (
								<TextControl
									label={__('Number part text', 'arkhe-blocks')}
									value={theNum || ''}
									onChange={(val) => {
										if (!IS_PRO) return;
										setAttributes({ theNum: val });
									}}
								/>
							)}
						</ArkbProOnly>
					</PanelBody>
				</InspectorControls>
				<div {...blockProps}>
					<div className={`${blockName}__head`}>
						<div
							className={`${blockName}__number`}
							style={numStyle}
							data-num={thisStepNum}
						>
							{thisStepLabel && <span className='__label'>{thisStepLabel}</span>}
						</div>
						<RichText
							placeholder={__('Enter text', 'arkhe-blocks') + '...'}
							className={`${blockName}__title`}
							tagName='div'
							value={title}
							onChange={(val) => setAttributes({ title: val })}
						/>
					</div>
					{!hideContent && <div {...innerBlocksProps} />}
				</div>
			</>
		);
	},
	save: ({ attributes }) => {
		const {
			title,
			numColor,
			stepLabel,
			theLabel,
			theNum,
			isHideLabel,
			isHideNum,
			hideContent,
		} = attributes;

		// ステップ番号の色設定
		let numStyle = null;
		if (numColor) {
			numStyle = { '--arkb-step-color': numColor };
		}

		const thisStepLabel = isHideLabel ? '' : theLabel || stepLabel;
		const thisStepNum = isHideNum ? '' : theNum || null;

		const blockProps = useBlockProps.save({
			className: `${blockName}__item`,
		});

		return (
			<div {...blockProps}>
				<div className={`${blockName}__head`}>
					<div className={`${blockName}__number`} style={numStyle} data-num={thisStepNum}>
						{thisStepLabel && <span className='__label'>{thisStepLabel}</span>}
					</div>
					{!!title && (
						<div className={`${blockName}__title`}>
							<RichText.Content value={title} />
						</div>
					)}
				</div>
				{!hideContent && (
					<div className={`${blockName}__body ark-keep-mt--s`}>
						<InnerBlocks.Content />
					</div>
				)}
			</div>
		);
	},
	deprecated,
});
