/**
 * @WordPress dependencies
 */
// import { __ } from '@wordpress/i18n';
import { RichText, InnerBlocks, useBlockProps } from '@wordpress/block-editor';

/**
 * export deprecated
 */
const blockName = 'ark-block-step';
export default [
	{
		supports: {
			anchor: true,
			className: false,
			reusable: false,
		},
		attributes: {
			title: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-step__title',
			},
			stepLabel: {
				type: 'string',
				default: 'STEP',
			},
			theLabel: {
				type: 'string',
				default: '',
			},
			theNum: {
				type: 'string',
				default: '',
			},
			numColor: {
				type: 'string',
				default: '',
			},
			isHideLabel: {
				type: 'boolean',
				default: false,
			},
			isHideNum: {
				type: 'boolean',
				default: false,
			},
		},
		save: ({ attributes }) => {
			const { title, numColor, stepLabel, theLabel, theNum, isHideLabel, isHideNum } =
				attributes;

			// ステップ番号の色設定
			let numStyle = null;
			let numClass = `${blockName}__number`;
			if (numColor) {
				numStyle = { '--ark-step_color': numColor };
				numClass += ' -has-color';
			}

			const thisStepLabel = isHideLabel ? '' : theLabel || stepLabel;
			const thisStepNum = isHideNum ? '' : theNum || null;

			const blockProps = useBlockProps.save({
				className: `${blockName}__item`,
			});

			return (
				<div {...blockProps}>
					<div className={`${blockName}__head`}>
						<div className={`${numClass}`} style={numStyle} data-num={thisStepNum}>
							{thisStepLabel && <span className='__label'>{thisStepLabel}</span>}
						</div>
						{!!title && (
							<div className={`${blockName}__title`}>
								<RichText.Content value={title} />
							</div>
						)}
					</div>
					<div className={`${blockName}__body ark-keep-mt--s`}>
						<InnerBlocks.Content />
					</div>
				</div>
			);
		},
	},
];
